.gpt3__blog-container_article{
    width: 100%;
    height: 100%;
    display: flex;
    background: var(--color-footer);
    flex-direction: column;
}

.gpt3__blog-container_article-image{
    width: 100%;
    height: 100%;
    background: var(--color-bg);
}

.gpt3__blog-container_article-image img{
    width: 100%;
    height: 100%;
}

.gpt3__blog-container_article-content{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    padding: 1rem 1.5rem;
}

.gpt3__blog-container_article-content p{
    font-family: var(--font-family);
    color: #FFFFFF;
    line-height: 35px;
    font-size: 11.65px;
    font-weight: 700;
}

.gpt3__blog-container_article-content h3{
    font-family: var(--font-family);
    font-weight: 800;
    color: #FFFFFF;
    font-size: 25.11px;
    line-height: 30.30px;
    margin-bottom: 5rem;
    cursor: pointer;
}

.gpt3__blog-container_article-content p:last-child{
    margin-bottom: 5rem;
    cursor: pointer;
}

@media screen and (max-width: 550px) {
    .gpt3__blog-container_article-content h3{
        font-size: 18px;
        line-height: 25px;
    }
}