.gpt3__features{
    display: flex;
    flex-direction: row;
}

.gpt3__features-heading{
    flex:1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    text-align: left;
    margin-right: 5rem;
}

.gpt3__features-heading h1{
    font-family: var(--font-family);
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
}

.gpt3__features-heading p{
    font-family: var(--font-family);
    color: var(--color-subtext);
    font-size: 16px;
    line-height: 30px;
    font-weight: 500;
    margin-top: 2rem ;
    cursor: pointer;
}

.gpt3__features-container{
    flex: 1.5;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

@media screen and (max-width:990px) {
    .gpt3__features{
        flex-direction: column;
    }
    
    .gpt3__features-heading{
        margin-bottom: 2rem;
    }
}

@media screen and (max-width:990px) {
    .gpt3__features-heading{
        font-size: 28px;
        line-height: 38px;
    }
}