.gpt3__navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;
}

.gpt3__navbar-links{
    flex:1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.gpt3__navbar-links_logo{
    margin-right: 2rem;
}

.gpt3__navbar-links_logo img{
    width: 62.56px;
    height: 16.02px;
}

.gpt3__navbar-links-container{
    display: flex;
    flex-direction: row;
}

.gpt3__navbar-sign{
    display: flex;
    justify-content: flex-end;
    align-items: center;   
}

.gpt3__navbar-links-container p,
.gpt3__navbar-sign p,
.gpt3__navbar-menu_container-links p,
.gpt3__navbar-menu_container-links-sign p{
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}

.gpt3__navbar-sign button,
.gpt3__navbar-menu_container-links-sign button{
    padding: 0.5rem 1rem;
    color: #fff;
    background: #ff4820;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border-radius: 5px;
    border: 0;
    outline: none;
    cursor:pointer;
}


.gpt3__navbar-menu{
    margin-left: 1rem;
    position: relative;
    display: none;
}

.gpt3__navbar-menu svg{
    cursor: pointer;
}

.gpt3__navbar-menu-container{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    position: absolute;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    right: 0;
    top: 40px;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
}

.gpt3__navbar-menu_container-links p,
.gpt3__navbar-menu_container-links-sign button{
    margin: 2rem 0;
}

.gpt3__navbar-menu-container-links-sign{
    display: none;   
}



@media screen and (max-width:1050px){
    .gpt3__navbar-links-container{
        display: none;
    }
    .gpt3__navbar-menu{
        display: flex;
    }

    .gpt3__navbar-menu_container-links-sign{
        display: none;
    }
    
}

@media screen and (max-width:700px) {
    .gpt3__navbar{
        padding: 2rem 4rem;
    }
}

@media screen and (max-width:550px) {
    .gpt3__navbar{
        padding: 2rem;
    }

    .gpt3__navbar-sign{
        display: none;
    }

    .gpt3__navbar-menu_container-links-sign{
        display: block;
    }

    .gpt3__navbar-menu-container{
        top: 20px;
    }
}